import React from "react";

const CaveContent = () => {
  return (
    <div className="content" id="skills">
      <h3>Skills</h3>
      <p className="m-5 d-flex gap-5">
        <div className="me-5">
          <h5>Front End :</h5>
          <div>
            <img className="sIcon" src="Ui/html.png" alt="" />
            <p className="ms-5"> HTML </p>
          </div>
          <div>
            <img className="sIcon" src="Ui/css.png" alt="" />
            <p className="ms-5"> CSS </p>
          </div>
          <div>
            <img className="sIcon" src="Ui/js.png" alt="" />
            <p className="ms-5"> JavaScript </p>
          </div>
          <div>
            <img className="sIcon" src="Ui/react.png" alt="" />
            <p className="ms-5"> React </p>
          </div>
        </div>

        <div className="ms-5">
          <h5> Back End :</h5>
          <div>
            <img className="sIcon" src="Ui/dj.png" alt="" />
            <p className="ms-5"> Django </p>
          </div>
        </div>
      </p>
    </div>
  );
};

export default CaveContent;
