import React from "react";
import SimpleParallax from "simple-parallax-js";

const Forest = () => {
  return (
    <div className="parallax forest" id="home">
      <SimpleParallax orientation="down" delay={0.3} overflow transition="ease">
        <img src="forest/forest_back.png" alt="forest background" />
      </SimpleParallax>
      <SimpleParallax orientation="down" delay={0.5} overflow transition="ease">
        <img src="forest/forest_mid.png" alt="forest middle" />
      </SimpleParallax>
      <SimpleParallax orientation="down" delay={0.7} overflow transition="ease">
        <img src="forest/forest_front.png" alt="forest front" />
      </SimpleParallax>

      <a href="#footer" className="position-absolute end-0 bottom-0 display-6">Contact Me</a>
    </div>
  );
};

export default Forest;
