import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { useEffect } from "react";
import Aboutme from "./contents/aboutMe";
import Skills from "./contents/Skills";
import Portfolio from "./contents/Portfolio";
gsap.registerPlugin(ScrollTrigger);

const Cave = () => {
  useEffect(() => {
    let sections = gsap.utils.toArray(".slide");

    // Horizontal scrolling
    gsap.to(sections, {
      xPercent: -100 * (sections.length - 1),
      scrollTrigger: {
        trigger: ".main",
        pin: ".main",
        scrub: 1,
        end: "+=6000",
      },
    });

    // Apply parallax effect to each slide individually
    sections.forEach((slide) => {
      const caveImages = {
        back: slide.querySelector("img:nth-child(1)"), // Back image
        mid: slide.querySelector("img:nth-child(2)"), // Middle image
        front: slide.querySelector("img:nth-child(3)"), // Front image
      };

      // Move the background
      gsap.to(caveImages.back, {
        x: "40%", // Adjust for desired effect
        scrollTrigger: {
          trigger: slide,
          start: "left",
          end: "+=6000",
          scrub: 1,
        },
      });

      // Move the middle image
      gsap.to(caveImages.mid, {
        x: "0%", // Adjust for desired effect
        scrollTrigger: {
          trigger: slide,
          start: "left",
          end: "+=6000",
          scrub: 1,
        },
      });

      // Keep the front image
      gsap.to(caveImages.front, {
        x: "10%", // No movement for the front image
        scrollTrigger: {
          trigger: slide,
          start: "left",
          end: "+=6000",
          scrub: 1,
        },
      });
    });

    return () => {
      ScrollTrigger.getAll().forEach((trigger) => trigger.kill());
    };
  }, []);

  return (
    <>
      <style>
        {`.main {
            display: flex;
            flex-wrap: nowrap;
            width: 300%;
            overflow-x: hidden;
            height: 100vh;
          }`}
      </style>

      <div className="main">
        <div className="slide parallax cave">
          <img src="cave/cave_back.png" alt="Cave background" />
          <img src="cave/cave_mid.png" alt="Cave middle" />
          <img src="cave/cave_front.png" alt="Cave front" />
          <Aboutme/>
        </div>
        <div className="slide parallax cave">
          <img src="cave/cave_back.png" alt="Cave background" />
          <img src="cave/cave_mid.png" alt="Cave middle" />
          <img src="cave/cave_front.png" alt="Cave front" />
          <Skills/>
        </div>
        <div className="slide parallax cave">
          <img src="cave/cave_back.png" alt="Cave background" />
          <img src="cave/cave_mid.png" alt="Cave middle" />
          <img src="cave/cave_front.png" alt="Cave front" />
          <Portfolio/>
        </div>
      </div>
    </>
  );
};

export default Cave;
